import CryptoJS from "crypto-js";

const addcode=(a,b)=>{
   let s=CryptoJS.AES.encrypt(""+a,b).toString();
   return s;
}

const pullcode=(a,b)=>{
    var bytes = CryptoJS.AES.decrypt(a,b);
    var originalText = bytes.toString(CryptoJS.enc.Utf8);
    return originalText;
}

const maxmin=(max,min)=>{
  return Math.floor(Math.random()*(max-min+1)+min);
}

const date=(date,type)=>{
  var y=date.getFullYear();
  var M=date.getMonth()+1;
  var d=date.getDate();
  var h=date.getHours();
  var m=date.getMinutes();
  var s=date.getSeconds();
  M=M<10?'0'+M:M;
  d=d<10?'0'+d:d;
  h=h<10?'0'+h:h;
  m=m<10?'0'+m:m;
  s=s<10?'0'+s:s;
  if(type==0){
    var hd=y+'-'+M+'-'+d
    return hd
  }else if(type==1){
    hd=y+'-'+M+'-'+d+'  '+h+':'+m+':'+s
    return hd
  }else{
    return false
  }
}
const classFor=(list,class_id)=>{
  for(let index in list){
    if(class_id==list[index].id){
      return list[index].name
    }
  }
}

const yestedate=()=>{
  var start1 = new Date(new Date(new Date().toLocaleDateString()).getTime()-24*60*60*1000+1)
  return start1
}

const todate=()=>{
  var start1 = new Date(new Date(new Date().toLocaleDateString()).getTime()-1)
  return start1
}

const copyobj=(o)=>{
  var obj=JSON.parse(JSON.stringify(o))
  return obj
}

const today=(var1)=>{
  var date=new Date(((new Date(var1).getTime())+24*60*60*1000)-1);
  var y=date.getFullYear();
  var M=date.getMonth()+1;
  var d=date.getDate();
  var h=date.getHours();
  var m=date.getMinutes();
  var s=date.getSeconds();
  M=M<10?'0'+M:M;
  d=d<10?'0'+d:d;
  h=h<10?'0'+h:h;
  m=m<10?'0'+m:m;
  s=s<10?'0'+s:s;
  return y+'-'+M+'-'+d+'  '+h+':'+m+':'+s
}

export default {
  addcode,pullcode,maxmin,date,yestedate,todate,classFor,copyobj,today
}



