<template>
  <div id='sale_view'>
    <!-- header -->
    <div class="fsb mg-10">
      <div>
        <el-button type="danger">营业概况</el-button>
        <el-button type="primary">交接班记录</el-button>
        <el-button type="primary">导出</el-button>
      </div>
      <div>
        <el-date-picker
          v-model="value1"
          type="daterange"
          range-separator="至"
          start-placeholder="开始日期"
          end-placeholder="结束日期"
          value-format="yyyy-MM-dd HH:mm:ss">
        </el-date-picker>
        <el-button type="warning" @click="getdata()">查询</el-button>
      </div>
    </div>
    <!-- table -->
    <Table :tableData='tableData' :table_title='table_title' :table_height='table_height' :proplist="proplist" ></Table>
  </div>
</template>
<script>
import Table from '../../components/table'
export default {
  components:{
    Table,
  },
  data() {
    return {
      value1:[this.$code.yestedate(),this.$code.todate()],
      tableData: [{
          index: '商品销售',
          allview:'销售额 5006.45,利润 466.34，单数 34',
          money:'0.00',
          yl:'0.00',
          zfb:'0.00',
          wx:'0.00',
        },{
          index: '商品退货',
          allview:'退货金额 45.24，单数 54',
          money:'0.00',
          yl:'-',
          zfb:'-',
          wx:'-',
        },{
          index: '营业实收',
          allview:'金额 0.00',
          money:'0.00',
          yl:'0.00',
          zfb:'0.00',
          wx:'0.00',
      }],
      table_title:['','概况','现金支付','银联支付','支付宝支付','微信支付'],
      proplist:['index','allview','money','yl','zfb','wx'],
      table_height:'',
    }
  },
  created() {
    this.getdata()
  },
  methods: {
    getdata(){
      console.log(this.value1)
      let data={
        start_date:this.value1[0],
        end_date:this.value1[1]
      }
      this.$api.salesoutlook(data,res=>{
        console.log(res)
      })
    }
  },
}
</script>



